
// Переменные
$bg: #FFFFFF;
$white: rgba(255, 255, 255, 1);
$text: rgba(31, 32, 65, 0.5);
$bg-gradient: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
$purpe: rgba(188, 156, 255, 1);
$shade_100:#1F2041;
$shade_75:rgba(31, 32, 65, 0.75);

// Шрифтrgb(97, 87, 119)
@font-face {
    font-family: 'NewFont';
    font-style: normal;
    font-weight: 400;
    src: url("/src/components/logo/fonts/fonts/NewFont\ Regular.woff"); 
  }
@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 100;
    src: url("./fonts/Montserrat/Montserrat-Thin.ttf");
  }
  @font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    src: url("./fonts/Montserrat/Montserrat-Regular.ttf");
  }
  @font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    src: url("./fonts/Montserrat/Montserrat-Bold.ttf");
  }
  

  // Общие правила
 a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
    text-decoration: none;
    list-style: none;
  }

html {
    font-size: 10px;
    background-color: rgb(180, 196, 196);
     
}
button {
    &:focus {
        outline: none;
    }
}
.container {
    max-width: 116rem;
    padding: 0 2rem;
    margin: 0 auto;
 }



