.nav {
    display: none;
    display: flex;
    min-width: 44.7rem;
    min-height: 2.1rem;
    justify-content: space-between;
    align-items: center;
    &-item {    
        margin-right: 2rem;   
        position: relative;         
    }
    &-item:last-child {    
        margin-right: 0.3rem;            
    }   
    &-item:first-child {    
        margin-right: 2rem;                      
    }   
    &-link {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 1.4rem;
        color: $text;
        line-height: 1.7rem;
        .more__img {
            padding-left: 0.5rem;
            padding-right: 0.5rem;
        }
    }
    .active {
        font-weight: 700;
    }
    @media (max-width: 768px) {
        display: none;
    }
}
