.btn-registr {
    padding: 1rem;
    background: $bg-gradient;
    cursor: pointer;
    font-size: 1.2rem;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    border-radius: 2.2rem;
    text-transform:uppercase;
    border: 1px solid #000000;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;
    padding-left: 1.6rem;
    padding-right: 2rem;
    color: $white;
}
.btn-enter {
    padding: 1rem;
    background: $white;
    cursor: pointer;
    font-size: 1.2rem;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    border-radius: 2.2rem;
    text-transform:uppercase;
    border: 0.2rem solid ;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;
    padding-left: 1.8rem;
    padding-right: 1.8rem;
    color: rgba(188, 156, 255, 1);
}
   
